.inspiration-repo .name {
  font-size: 32px;
  font-weight: lighter;
  letter-spacing: 4px;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
  transition: letter-spacing 0.5s; }
  .inspiration-repo .name:hover {
    letter-spacing: 10px; }

.inspiration-repo .content {
  position: relative;
  margin: 10px auto;
  max-width: 1000px;
  max-height: 20px;
  overflow: hidden;
  perspective: 1000px;
  transition: max-height 0.5s; }
  .inspiration-repo .content .detail {
    display: inline-block;
    position: relative;
    width: 50%; }
    .inspiration-repo .content .detail .description,
    .inspiration-repo .content .detail .link {
      box-sizing: border-box;
      padding: 0px 20px;
      font-size: 20px;
      font-weight: lighter;
      line-height: 28px;
      opacity: 0;
      transition: opacity 0.5s; }
    .inspiration-repo .content .detail .link {
      display: inline-block;
      margin-top: 20px;
      font-weight: bold;
      text-decoration: none;
      color: inherit; }
  .inspiration-repo .content .video {
    display: inline-block;
    position: absolute;
    top: 50%;
    max-width: 50%;
    max-height: 100%;
    transform: translateY(-50%);
    border: 1px solid #555555;
    box-sizing: border-box;
    opacity: 0;
    z-index: -1;
    transition: opacity 0.5s, transform 0.5s 0.25s; }
  .inspiration-repo .content.expanded {
    max-height: 90vh; }
    .inspiration-repo .content.expanded .detail .description,
    .inspiration-repo .content.expanded .detail .link {
      opacity: 1;
      transition: opacity 0.5s 0.25s; }
    .inspiration-repo .content.expanded .video {
      opacity: 1;
      transition: opacity 0.5s 0.25s, transform 0.5s 0.25s; }

.inspiration-repo:nth-child(odd) .content .detail {
  text-align: right;
  border-right: 1px solid; }
  .inspiration-repo:nth-child(odd) .content .detail .link {
    right: 0px; }

.inspiration-repo:nth-child(odd) .content .video {
  left: 50%;
  box-shadow: 20px 0px 0px -5px #555555; }

.inspiration-repo:nth-child(odd) .content.expanded .video {
  transform: translateY(-50%) matrix3d(0.7109, 0, 0.70328, 0, 0, 1, 0, 0, 0.70328, 0, 0.7109, 0, 0, 0, 0, 1.2); }

.inspiration-repo:nth-child(even) .content .detail {
  margin-left: 50%;
  border-left: 1px solid; }
  .inspiration-repo:nth-child(even) .content .detail .link {
    left: 0px; }

.inspiration-repo:nth-child(even) .content .video {
  right: 50%;
  box-shadow: -20px 0px 0px -5px #555555; }

.inspiration-repo:nth-child(even) .content.expanded .video {
  transform: translateY(-50%) matrix3d(0.7109, 0, -0.70328, 0, 0, 1, 0, 0, 0.70328, 0, 0.7109, 0, 0, 0, 0, 1.2); }

@media only screen and (max-width: 650px) {
  .inspiration-repo .name {
    font-size: 24px; }
  .inspiration-repo .content .detail {
    width: 100%; }
    .inspiration-repo .content .detail .description,
    .inspiration-repo .content .detail .link {
      font-size: 16px;
      line-height: 21px; }
    .inspiration-repo .content .detail .link {
      text-align: center; }
    .inspiration-repo .content .detail .description {
      max-width: 500px;
      margin: auto;
      text-align: left; }
    .inspiration-repo .content .detail .link {
      display: block;
      width: 100%;
      text-align: center; }
  .inspiration-repo .content .video {
    display: none; }
  .inspiration-repo:nth-child(odd) .content .detail, .inspiration-repo:nth-child(even) .content .detail {
    margin-left: 0px;
    border-left: none;
    border-right: none; } }
