.logo-group .logo-path,
.logo-group .logo-dot {
  stroke: inherit;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  stroke-width: 0;
  fill: none; }

.logo-group .logo-path {
  stroke-width: 10;
  animation: show-logo-path 1s linear forwards; }

.logo-group .logo-dot {
  fill: inherit;
  opacity: 0;
  animation: show-logo-dot 1s linear forwards; }

@keyframes show-logo-path {
  0% {
    stroke-dashoffset: 1000; }
  75%,
  100% {
    stroke-dashoffset: 650; } }

@keyframes show-logo-dot {
  0%,
  75% {
    opacity: 0; }
  100% {
    opacity: 1; } }
