.module-repo .name {
  font-size: 32px;
  font-weight: lighter;
  letter-spacing: 4px;
  white-space: nowrap;
  cursor: pointer;
  transition: letter-spacing 0.5s; }
  .module-repo .name:hover {
    letter-spacing: 10px; }

.module-repo .detail {
  margin: 10px 0px 10px 20px;
  max-height: 20px;
  overflow: hidden;
  border-left: 1px solid;
  transition: max-height 0.5s; }
  .module-repo .detail.expanded {
    max-height: 300px; }
    .module-repo .detail.expanded .description,
    .module-repo .detail.expanded .link {
      opacity: 1; }
  .module-repo .detail .description,
  .module-repo .detail .link {
    padding: 0px 20px;
    font-size: 20px;
    font-weight: lighter;
    letter-spacing: 1.5px;
    line-height: 28px;
    opacity: 0;
    transition: opacity 0.5s; }
  .module-repo .detail .link {
    display: inline-block;
    margin-top: 20px;
    font-weight: bold;
    text-decoration: none;
    color: inherit; }

@media only screen and (max-width: 650px) {
  .module-repo .name {
    font-size: 24px; }
  .module-repo .detail .description,
  .module-repo .detail .link {
    font-size: 16px;
    line-height: 21px; } }
