body {
  font-family: Montserrat, sans-serif;
  margin: 0px;
  padding: 0px; }
  body #app {
    position: relative;
    min-height: 100vh;
    opacity: 0;
    animation: fade-in 0.5s forwards;
    overflow: hidden;
    background-color: #ffffff;
    color: #222222;
    stroke: #222222;
    fill: #222222;
    transition: color 0.5s, stroke 0.5s, fill 0.5s; }
    body #app.night {
      background-color: #070b34;
      color: #ffffff;
      stroke: #ffffff;
      fill: #ffffff; }
    body #app #app-header-wrapper {
      position: fixed;
      width: 100%;
      z-index: 1; }
      body #app #app-header-wrapper #app-header {
        position: relative;
        max-width: 1000px;
        margin: auto;
        padding: 0px 40px;
        box-sizing: border-box; }
        body #app #app-header-wrapper #app-header #app-logo {
          width: 80px;
          height: 60px;
          z-index: 1;
          transition: transform 0.5s;
          cursor: pointer; }
          body #app #app-header-wrapper #app-header #app-logo:hover {
            transform: scale(1.2); }
    body #app #contact-toggle {
      display: inline-block;
      padding: 20px 0px;
      font-weight: bold;
      text-align: right;
      float: right;
      z-index: 1;
      cursor: pointer; }
      body #app #contact-toggle .open,
      body #app #contact-toggle .close {
        display: inline-block;
        width: 100%;
        height: 20px;
        overflow: hidden;
        white-space: nowrap;
        transition: height 0.5s;
        float: left; }
      body #app #contact-toggle .close {
        height: 0px; }
      body #app #contact-toggle.opened {
        color: #ffffff; }
        body #app #contact-toggle.opened .open {
          height: 0px; }
        body #app #contact-toggle.opened .close {
          height: 20px; }
    body #app > section,
    body #app > #app-logo {
      opacity: 1; }
      body #app > section.hide,
      body #app > #app-logo.hide {
        opacity: 0;
        z-index: -1; }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes slide-up {
  from {
    padding-top: 20px;
    opacity: 0; }
  to {
    padding-top: 0px;
    opacity: 1; } }

@keyframes wobble {
  0%,
  100% {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%; }
  15% {
    -webkit-transform: translateX(-30px) rotate(-6deg);
    transform: translateX(-30px) rotate(-6deg); }
  30% {
    -webkit-transform: translateX(15px) rotate(6deg);
    transform: translateX(15px) rotate(6deg); }
  45% {
    -webkit-transform: translateX(-15px) rotate(-3.6deg);
    transform: translateX(-15px) rotate(-3.6deg); }
  60% {
    -webkit-transform: translateX(9px) rotate(2.4deg);
    transform: translateX(9px) rotate(2.4deg); }
  75% {
    -webkit-transform: translateX(-6px) rotate(-1.2deg);
    transform: translateX(-6px) rotate(-1.2deg); } }
