.shape-wrapper {
  position: absolute;
  z-index: -1;
  opacity: 0;
  animation: show-shape 1s forwards; }
  .shape-wrapper .shape {
    user-select: none;
    transform-origin: top left; }
    .shape-wrapper .shape.float1 {
      animation: floating1 ease-in-out infinite alternate; }
    .shape-wrapper .shape.float2 {
      animation: floating2 ease-in-out infinite alternate; }

@keyframes show-shape {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes floating1 {
  0% {
    transform: translateX(0%) rotate(0deg); }
  100% {
    transform: translateX(10%) rotate(30deg); } }

@keyframes floating2 {
  0% {
    transform: translateX(0%) rotate(0deg); }
  100% {
    transform: translateX(-10%) rotate(-20deg); } }
