#intro .headline {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center; }
  #intro .headline .title,
  #intro .headline .subtitle {
    display: inline-block;
    opacity: 0; }
  #intro .headline .title {
    font-size: 50px;
    animation: fade-in 0.5s forwards, wobble 1s forwards; }
  #intro .headline .subtitle {
    animation: fade-in 0.5s 0.5s forwards; }

@media only screen and (max-width: 650px) {
  #intro .headline .title {
    font-size: 46px; } }
