.signature-clip .clip-stroke {
  stroke: inherit;
  stroke-width: 1; }

.signature-stroke {
  stroke: inherit;
  stroke-linecap: round;
  fill: none; }
  .signature-stroke .stroke-J,
  .signature-stroke .stroke-dot,
  .signature-stroke .stroke-W,
  .signature-stroke .stroke-ong {
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
    stroke-width: 0;
    transition: 0.3s; }

#contact {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: left 0s 0.5s, opacity 0.5s;
  overflow: hidden; }
  #contact.show {
    transition: left 0s, opacity 0.5s;
    opacity: 1;
    left: 0px; }
    #contact.show .signature .signature-stroke .stroke-J {
      transition: 0.5s 0.5s; }
    #contact.show .signature .signature-stroke .stroke-dot {
      transition: 0.3s 1s; }
    #contact.show .signature .signature-stroke .stroke-W {
      transition: 1s 1.5s; }
    #contact.show .signature .signature-stroke .stroke-ong {
      transition: 1s 2.5s; }
    #contact.show .signature .signature-stroke .stroke-J {
      stroke-dashoffset: 200;
      stroke-width: 10; }
    #contact.show .signature .signature-stroke .stroke-dot {
      stroke-dashoffset: 950;
      stroke-width: 10; }
    #contact.show .signature .signature-stroke .stroke-W {
      stroke-dashoffset: 0;
      stroke-width: 10; }
    #contact.show .signature .signature-stroke .stroke-ong {
      stroke-dashoffset: 0;
      stroke-width: 10; }
  #contact .signature {
    position: absolute;
    top: 5%;
    left: 50%;
    width: 100%;
    transform: translate(-52%, 0%) rotate(-5deg);
    stroke: #ffffff;
    opacity: 0.4;
    z-index: -1; }
  #contact .comment {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 90%;
    max-width: 400px;
    transform: translate(-50%, -50%);
    font-size: 20px;
    color: #ffffff; }
  #contact .button-panel {
    position: absolute;
    bottom: 0%;
    left: 50%;
    width: 80%;
    max-width: 400px;
    margin: 0px auto 10vh auto;
    text-align: center;
    transform: translate(-50%, 0%); }
    #contact .button-panel:hover .link:hover .icon {
      transform: scale(1.2); }
    #contact .button-panel .button-wrapper {
      position: relative;
      display: inline-block;
      width: 33%; }
      #contact .button-panel .button-wrapper .link {
        display: inline-block;
        width: 100%;
        stroke-width: 0px; }
        #contact .button-panel .button-wrapper .link .icon {
          fill: #ffffff;
          width: 40px;
          height: 40px;
          transition: transform 0.3s; }
        #contact .button-panel .button-wrapper .link:hover + .name {
          height: 30px;
          opacity: 1; }
      #contact .button-panel .button-wrapper .name {
        position: absolute;
        bottom: 100%;
        left: 50%;
        height: 0px;
        transform: translate(-50%, 0%);
        color: #ffffff;
        opacity: 0;
        overflow: hidden;
        transition: opacity 0.5s, height 0.5s; }
  @media only screen and (max-width: 650px) {
    #contact .signature {
      opacity: 0.4; }
    #contact .comment {
      font-size: 16px; }
    #contact .button-panel .button-wrapper .link .icon {
      width: 30px;
      height: 30px; } }
