section {
  position: relative;
  padding: 40px;
  margin: auto;
  box-sizing: border-box;
  width: 100%;
  max-width: 1000px;
  min-height: 100vh; }
  section .headline {
    display: inline-block;
    width: 90%;
    max-width: 450px; }
  section .title {
    display: inline-block;
    padding-bottom: 20px;
    font-size: 48px;
    font-weight: bold; }
  section .subtitle {
    font-size: 20px;
    font-weight: lighter;
    line-height: 28px; }
  section .list {
    display: inline-block;
    padding: 60px 0px;
    width: 100%; }
  @media only screen and (max-width: 650px) {
    section .title {
      font-size: 36px; }
    section .subtitle {
      font-size: 16px;
      line-height: 21px; } }
